export const NavBar = () => {
    return <div className={`page-header navbar px-5 py-3`}>
        <div className="page-header-inner ">
            <div className="page-logo">
                <ImageField isPrefix={true} className={`img-fluid`} src={LOGOBRAND} />
            </div>
        </div>
    </div>
}
const InitPage = ({ signUpData, selectProduct }) => {

    const [state, setState] = useState({
        data: [],
        showPop: false,
        loading: false
    })
    const dispatch = useDispatch();
    const navigate = useNavigate();

    useEffect(() => {
        // ADD_SCRIPT("https://assets.calendly.com/assets/external/widget.js");
        if (!signUpData) {
            localStorage.clear();
            window.location.href = "/";
        } else {
            if (IS_TAGEMBED) {
                const route = localStorage.getItem('sign-up-route');
                setTimeout(() => {
                    navigate(route);
                    localStorage.removeItem('sign-up-route');
                }, 3000);
            } else {
                setState(prev => ({ ...prev, data: products }));
                getProduct();
            }
        }
    }, [])

    // select product to skip user from selecting product in case of coming from widget and display directly.
    const getProduct = () => {
        const productId = window.location.pathname.includes("widget") ? 6 : null;
        if (productId) onSubmit(productId);
    }

    const onSubmit = (id) => {
        setState(prev => ({ ...prev, loading: true }));
        localStorage.setItem("isProductSelected", true);
        selectProduct({ ...signUpData, activeProduct: String(id == 4 ? 4 : 6) }, navigate, () => {
            if (id == 2) {
                localStorage.setItem("isEcommerce", true);
                SkipOnBoarding({ "stepstype": "onboardingskip" }).then(res => {
                    dispatch(getUserInfo_afterChanges())
                }).catch(ex => { })
            }
        })
    }

    return (
        IS_TAGEMBED ? <div className='f-center' style={{ minHeight: 'calc(100vh - 100px)' }}><Loader /></div> : <>
            <NavBar />
            <div className="container login-container" id="controllerContent">
                <div className="controller" id="init">
                    <div className="action" id="initindex">
                        <div className="mainLogin noLoader" id="WallIndexForm">
                            <div className="sourceSection init_sec">
                                <div className="loginBlock loginBlock_m_p loginBlock_ch freeSocialWall picASource">
                                    <div className="loginInnSec" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginTop: '20px' }}>
                                        <div className="loginHead text-center">
                                            <h2 id="initheader">Choose the Experience That Matches Your Brand Goals</h2>
                                            <p className='text-muted'>Tell us how you plan to use {IS_TAGEMBED ? "Tagembed" : "Tagbox"}. This helps us tailor your setup to your exact needs.</p>
                                        </div>
                                        <div className="onboarding-form pt-5">
                                            <div id="onboardingList" className="container" style={{ maxWidth: '960px' }}>
                                                <ul id="onbordingInput" className="onboardings row px-0">
                                                    {state.data.map((item, index) => <ProductCard {...item} selectProduct={onSubmit} loading={state.loading} key={index} />)}
                                                </ul>
                                                {/* <div className="start-button-box">
                                                <p className="calendlyAclick d-flex align-items-center justify-content-center"><span className="me-2">Not Sure? </span> <span className="btn btn-link" onClick={() => setState(prev => ({ ...prev, showPop: true }))}>Discuss With Us</span>
                                                </p>
                                            </div> */}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>)
}
const mapStateToProps = (state) => {
    return {
        signUpData: state.authorized?.userData?.user_data
    }
}

const mapDispatchToProps = dispatch => {
    return {
        selectProduct: (data, history, callback) => dispatch(productSignUp(data, history, callback)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(InitPage);

const ImageField = ({ alt, className, src, ...rest }) => (
    <img {...rest} src={src} alt={alt} className={className} />
);