import React, { Suspense, useState, useEffect } from "react";
// import Login from "../Authentication/Login";
// import SignUp from '../Authentication/SignUp'
// import ForgotPassword from '../Authentication/ForgotPassword'
// import ResetPassword from '../Authentication/ResetPassword'

import { Routes, Route, Navigate, useParams, useNavigate, useLocation } from "react-router-dom";
import StripeCallback from '../Layout/Main/PricePlan/StripeCallback';

// import MoreInfo from '../Authentication/MoreInfo'
// import VerifyEmail from '../Authentication/verifyEmail'
// import AccountVerify from '../Authentication/Components/AccountVerify'
// import PricePlan from "../Layout/Main/PricePlan/SignUpPricePlan";
// import Authentication from "../Authentication";
import ErrorFound from "../ErrorFound";
import { BIGCOMMERCE_LOAD_LOGIN, installShopify, loginWithBigcommerce, loginWithShopify } from "../../actions/loginActions";
import { Loader } from "../Layout/Main/CommonComponents/Loader";
import PageHead from "./pageTitle";
import Authorization from '../../utils/Authorization';
import UgcRightsAction from "../Layout/Main/UgcRightsAction";
import ShopifyInstall from "../Authentication/ShopifyInstall"
import ShopifyPlanCallback from "../Layout/Main/PricePlan/ShopifyPlanCallback";
import ColabSignUp from "../Authentication/ColabSignUp";
import TempPassword from "../Authentication/TempPassword";
import InitPage from "../Authentication/Init";
import UgcSuitTiktok from "../Layout/Main/CommonComponents/UgcSuitTiktok";
import { AutoLogin } from "./ExtendRoutes";
//const ShareAccount = React.lazy(() => import("../components/shareAccount"));

const Login = React.lazy(() => import("../Authentication/Login"));
const SignUp = React.lazy(() => import("../Authentication/SignUp"));
const ForgotPassword = React.lazy(() => import("../Authentication/ForgotPassword"));
const ResetPassword = React.lazy(() => import("../Authentication/ResetPassword"));
const MoreInfo = React.lazy(() => import("../Authentication/MoreInfo"));
const VerifyEmail = React.lazy(() => import("../Authentication/Verification"));
const AccountVerify = React.lazy(() => import("../Authentication/Components/AccountVerify"));
const PricePlan = React.lazy(() => import("../Layout/Main/PricePlan/SignUpPricePlan"));
const Authentication = React.lazy(() => import("../Authentication"));
const ShareAccount = React.lazy(() => import("../Layout/Main/SocialAccount/ShareAccount"));

const UpdateResetPassword = (props) => {
  const params = useParams();
  return <Suspense fallback={null}><ResetPassword prefix={props.prefix} params={params} /></Suspense>
}

const UpdateVerifyEmail = (props) => {
  const params = useParams();
  return <Suspense fallback={null}><VerifyEmail prefix={props.prefix} params={params} history={props.history} /></Suspense>
}

const SwitchInTaggshop = (props) => {
  const location = useLocation();
  const isToken = new URLSearchParams(location.search).get("token");
  const callback = new URLSearchParams(location.search).get("callback");
  if (isToken) {
    localStorage.setItem("token", isToken)
    window.location.href = callback
  }
  return <div className='h-100vh w-100 f-center'><Loader /></div>
}

const BigCommerceAuth = ({ history }) => {

  const location = useLocation();
  const code = new URLSearchParams(location.search).get("code");
  const account_uuid = new URLSearchParams(location.search).get("account_uuid");
  const context = new URLSearchParams(location.search).get("context");
  const scope = new URLSearchParams(location.search).get("scope");

  useEffect(() => {
    loginWithBigcommerce({ code, account_uuid, context, scope }, history);
  }, []);

  return <div className='h-100vh w-100 f-center'><Loader /></div>
}

const BigCommercLogin = () => {
  const location = useLocation()
  const signed_payload = new URLSearchParams(location.search).get("signed_payload")
  const signed_payload_jwt = new URLSearchParams(location.search).get("signed_payload_jwt")
  useEffect(() => {
    BIGCOMMERCE_LOAD_LOGIN({ signed_payload, signed_payload_jwt })
  }, [])

  return <div className='h-100vh w-100 f-center'><Loader /></div>
}

export default function LoginBasePage(props) {
  const navigate = useNavigate()
  useEffect(() => {
    if (!localStorage.getItem("token") && !window.location.pathname.includes('/accounts') && !window.location.pathname.includes('/connection') && !window.location.pathname.includes('/bigcommerce') && !window.location.pathname.includes('/Shopify') && !window.location.pathname.includes('/autologin') && !window.location.pathname.includes('/redirectlogin') && !window.location.pathname.includes('/ugc_right') && !window.location.pathname.includes('/feeds/authCallback') && !window.location.pathname.includes('/collaborator/request')) {
      navigate('/')
    }
  }, [])

  return (
    <ErrorFound>
      <PageHead />
      <Routes>
        <Route exact path="/autologin" element={<SwitchInTaggshop history={props.history} />} />
        <Route exact path="/redirectlogin/:token/:admin" element={<AutoLogin />} />
        <Route exact path="/redirectlogin/:token" element={<AutoLogin />} />

        {/* authorization route for ugc suit tiktok */}
        <Route exact path="/tiktok/feeds/authcallback/" element={<ErrorFound key={window.location.href}><UgcSuitTiktok /></ErrorFound>} />
        <Route exact path="/" element={<Suspense fallback={null}><Authentication><Login prefix="/" history={navigate} /></Authentication></Suspense>} />
        <Route exact path="/accounts/register" element={<Suspense fallback={null}><Authentication><SignUp prefix="/" history={navigate} /></Authentication></Suspense>} />
        <Route exact path="/accounts/login" element={<Suspense fallback={null}><Authentication><Login prefix="/" history={navigate} /></Authentication></Suspense>} />
        <Route exact path="/accounts/forgotpassword" element={<Suspense fallback={null}><Authentication><ForgotPassword prefix="/" activeProduct="0" /></Authentication></Suspense>} />
        <Route exact path="/accounts/temp-pwd" element={<Suspense fallback={null}><Authentication><TempPassword prefix="/" activeProduct="0" history={navigate} /></Authentication></Suspense>} />
        <Route exact path="/accounts/resetpassword/:userId/:token" element={<Suspense fallback={null}><Authentication><UpdateResetPassword prefix="/" /></Authentication></Suspense>} />

        <Route exact path="/accounts/otherinfo" element={<Suspense fallback={null}><Authentication><MoreInfo prefix="/" addEmail={false} history={props.history}/></Authentication></Suspense>} />
        <Route exact path="/accounts/extemail" element={<Suspense fallback={null}><Authentication><MoreInfo addEmail={true} prefix="/" history={props.history} /></Authentication></Suspense>} />

        <Route exact path="/accounts/verifyemail/:id" element={<UpdateVerifyEmail prefix="/" history={props.history} />} />

        <Route exact path="/init" element={<InitPage history={props.history} />} />

        <Route exact path="/feeds/authCallback" element={<Authorization />} />
        <Route exact path="/:product/feeds/authCallback" element={<Authorization />} />
        <Route exact path="/accounts/paymentPaidCallback" element={<StripeCallback />} />
        <Route exact path="/ugc_right/:id" element={<UgcRightsAction />} />

        <Route path="/Shopify/token" element={<ShopifyInstall history={navigate} isInstall={false} />} />
        <Route path="/Shopify/token/tagembed" element={<ShopifyInstall history={navigate} isInstall={false} tagembed={true}/>} />
        <Route path="/Shopify/install" element={<ShopifyInstall history={navigate} isInstall={true} />} />
        <Route path="/Shopify/install/tagembed" element={<ShopifyInstall history={navigate} isInstall={true} tagembed={true}/>} />
        <Route path="/bigcommerce/oauth" element={<BigCommerceAuth history={navigate} />} />
        <Route path="/bigcommerce/load" element={<BigCommercLogin />} />
        <Route exact path="/connection/:id" element={<Suspense fallback={null}><ShareAccount /></Suspense>} />

        <Route exact path="/accounts/shopifypaymentcomplete" element={<ShopifyPlanCallback />} />
        <Route path="/collaborator/request/:ownerToken/:collaboratorToken" element={<Authentication><ColabSignUp history={navigate} /></Authentication>}></Route>

        <Route path="*" element={<Navigate to={`/accounts/login`} />} />

      </Routes>

    </ErrorFound>
  )
}